.todo {
	display: flex;
	justify-content: space-between;
	background-color: #fff;
	border-radius: 15px;
	border: 1px solid transparent;
	padding: 20px 40px 20px 80px;
	font-size: 20px;
	margin: 0 auto 20px auto;
	width: 100%;
	position: relative;
	z-index: 1;
	transition: background-color 0.3s;

	&::before {
		content: "";
		display: block;
		position: absolute;
		background-image: url(./../../img/note.svg);
		width: 30px;
		height: 30px;
		left: 30px;
		top: 18px;
		z-index: 2;
	}
}

.completedTodo {
	background-color: lightgray;
	border-color: gray;
	color: gray;
}

.buttonsWrapper {
	display: flex;
}

.buttonTrash,
.buttonCheck {
	display: flex;
	align-items: center;
	justify-content: center;
	transition: transform 0.3s;
}

.buttonTrash::before,
.buttonCheck::before {
	content: "";
	display: block;
	width: 25px;
	height: 25px;
	background-repeat: no-repeat;
	background-position: center;
}

.buttonTrash::before {
	background-image: url(../../img/trash.svg);
}

.buttonCheck::before {
	background-image: url(../../img/check.svg);
}

.buttonTrash {
	margin-right: 20px;
}

.buttonTrash:hover,
.buttonCheck:hover {
	transform: scale(1.3);
}
