.form {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 24px;
	margin-bottom: 20px;
}

.inputForm {
	background-color: #fff;
	border: 1px solid transparent;
	border-radius: 15px;
	padding: 15px;
	align-self: stretch;
	transition: border-color ease-in-out 0.3s, background-color ease-in-out 0.3s;
}

.inputForm {
	margin-right: 20px;
}

.inputForm {
	&:hover {
		border-color: #2a5ce7;
	}
}

@media screen and (max-width: 520px) {
	.form {
		flex-direction: column;
	}

	.inputForm {
		margin-bottom: 20px;
		margin-right: 0;
	}

	.buttonForm {
		max-width: 250px;
		width: 100%;
		margin: 0 auto;
	}
}
