.buttonForm {
	background-color: #fff;
	border: 1px solid transparent;
	border-radius: 15px;
	font-size: 22px;
	padding: 20px;
	transition: border-color ease-in-out 0.3s, background-color ease-in-out 0.3s;
	line-height: 1;
	width: fit-content;

	&:hover {
		background-color: #ebe89c;
		border-color: #2a5ce7;
	}

	&:disabled {
		background-color: lightgrey;
		border-color: white;
		color: grey;
		cursor: default;
	}
}
